@import url(https://fonts.googleapis.com/css?family=Fira+Sans);
.Pokecard {
  text-align: center;
  font-family: 'Fira Sans', sans-serif;
  font-size: 24px;
  background: rgb(232, 203, 235);
  margin: 1rem;
  padding: 10px;
  border-radius: 3px;
  width: 250px;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
}

.Pokecard-image {
  display: flex;
  justify-content: center;
  margin: 5px;
  height: 200px;
}

.Pokecard img {
  -webkit-transition: all ease 500ms;
  transition: all ease 500ms;
}

.Pokecard img:hover {
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
  opacity: 0.7;
}

* {
  font-family: 'Fira Sans', sans-serif;
  color: rgb(84, 39, 114);
  margin: 0;
  padding: 0;
  text-align: center;
}

h2 {
  padding-top: 15px;
}

.Pokedex {
  background: rgb(207, 181, 231);
  border-radius: 5px;
  margin: 20px;
}

.Pokedex-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: space-evenly;
  padding-bottom: 20px;
}

